@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@200;300;400;600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  display: flex;
  flex-direction: column;
  margin-left: 5px;
  margin-right: 5px;
}

.navigationMenu {
  font-size: 25px;
  font-family: 'Raleway';
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

a:link {
  text-decoration: none;
  color: blue;
}

a:visited {
  text-decoration: none;
  color: blue;
}

.home {
  text-align: center;
}
body {
  overflow-x: hidden;
  height: 100%;
}

.bodyContainer {
  margin-bottom: 100px;
}

.name {
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin-bottom: -20px;
}

.svgAnimation {
  width: 100px;
  height: 120px;
  display: flex;
  overflow: hidden;
  background: rgba(255, 255, 255, 0.2);
  margin-right: -10px;
  margin-left: -7px;
}

.item {
  width: 56%;
  overflow: visible;
  stroke: #000000;
  stroke-width: 2;
  stroke-linejoin: round;
  stroke-linecap: round;
}

.typewriter {
  font-size: 30px;
}

.cursor {
  font-size: 30px;
  -webkit-animation-name: blink;
          animation-name: blink;
  -webkit-animation-duration: 800ms;
          animation-duration: 800ms;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  opacity: 1;
}

@-webkit-keyframes blink {
  from { opacity: 1; }
  to { opacity: 0; }
}

@keyframes blink {
  from { opacity: 1; }
  to { opacity: 0; }
}

.profilePicture {
  border-radius: 50%;
  width: 180px;
}

.bodyTitle {
  font-family: 'Raleway';
}

.bodyText {
  text-align: left;
}

.footer {
  align-items: center;
  flex-direction: column;
  height: 100px;
  margin-top: auto;
}
.parent {
  display: flex;
  text-align: left;
  flex-direction: column;
}

.header {
  font-family: 'Raleway';
  display: flex;
}

.subHeader {
  font-family: 'Raleway';
  display: flex;
}

.projectTitles {
  font-family: 'Raleway';
  font-size: 20px;
  margin-bottom: 0px;
  margin-top: 40px
}

.wearableImages {
  display: flex;
  justify-content: space-evenly;
}

.prototypeText {
  text-align: center;
}

.applyWithApiVideoContainer {
  position: relative;
  padding-bottom: 62.5%;
  height: 0;
}

.applyWithApiVideo {
  position: absolute; 
  top: 0; 
  left: 0; 
  width: 100%; 
  height: 100%;
}

.applyWithApiImage {
  max-width: 500px;
  max-height: 400px;
}
.parent {
  text-align: left;
}

.header {
  font-family: 'Raleway';
  align-items: flex-start;
}

.bodyTitles {
  font-family: 'Raleway';
  text-align: left;
  font-size: 20px;
  margin-bottom: 0px;
}
